@import url('https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap');
@import 'common.css';
body {
  font-family: 'Josefin Sans', sans-serif; }
.container {
  padding: 0 60px;
  box-sizing: border-box; }
.appWrap {
  position: relative;
  overflow: hidden; }

@keyframes rotate {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }

